@font-face {
  font-family: 'Rubik';
  src: local('Rubik Light'), local('Rubik-Light'), url('../../fonts/Rubiklight.woff2') format('woff2'), url('../../fonts/Rubiklight.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: optional;
}
@font-face {
  font-family: 'Rubik';
  src: local('Rubik'), local('Rubik-Regular'), url('../../fonts/Rubik.woff2') format('woff2'), url('../../fonts/Rubik.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: optional;
}
@font-face {
  font-family: 'Rubik';
  src: local('Rubik Bold'), local('Rubik-Bold'), url('../../fonts/Rubikbold.woff2') format('woff2'), url('../../fonts/Rubikbold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: optional;
}
/* New FONTS*/
@font-face {
  font-family: 'Caveat';
  src: local('Caveat Regular'), local('Caveat-Regular'), url('../../fonts/Caveat-Regular.woff2') format('woff2'), url('../../fonts/Caveat-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Caveat';
  src: local('Caveat Bold'), local('Caveat-Bold'), url('../../fonts/Caveat-Bold.woff2') format('woff2'), url('../../fonts/Caveat-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
.caveat {
  font-family: 'Caveat';
  font-size: 44px;
  color: #15171a;
  font-weight: bold;
  line-height: 1.2;
  padding-bottom: 30px;
}
@media (max-width: 768px) {
  .caveat {
    font-size: 28px;
    text-align: center;
    padding-bottom: 15px;
  }
}
@font-face {
  font-family: 'Urbanist';
  src: local('Urbanist Italic'), local('Urbanist-Italic'), url('../../fonts/Urbanist-Italic.woff2') format('woff2'), url('../../fonts/Urbanist-Italic.woff') format('woff');
