  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: 'Urbanist';
  src: local('Urbanist Regular'), local('Urbanist-Regular'), url('../../fonts/Urbanist-Regular.woff2') format('woff2'), url('../../fonts/Urbanist-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Zen Maru Gothic';
  src: local('Zen Maru Gothic Bold'), local('ZenMaruGothic-Bold'), url('../../fonts/ZenMaruGothic-Bold.woff2') format('woff2'), url('../../fonts/ZenMaruGothic-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Zen Maru Gothic';
  src: local('Zen Maru Gothic Light'), local('ZenMaruGothic-Light'), url('../../fonts/ZenMaruGothic-Light.woff2') format('woff2'), url('../../fonts/ZenMaruGothic-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Zen Maru Gothic';
  src: local('Zen Maru Gothic Medium'), local('ZenMaruGothic-Medium'), url('../../fonts/ZenMaruGothic-Medium.woff2') format('woff2'), url('../../fonts/ZenMaruGothic-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Zen Maru Gothic';
  src: local('Zen Maru Gothic Regular'), local('ZenMaruGothic-Regular'), url('../../fonts/ZenMaruGothic-Regular.woff2') format('woff2'), url('../../fonts/ZenMaruGothic-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 5px 5px 10px;
  padding: 0px;
  font-family: 'Zen Maru Gothic', "Arial", sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.5;
  color: #15171a;
  background-color: #FAF8F6;
}
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
}
.page {
  height: 100%;
  margin: 20px;
}
@media (max-width: 425px) {
  .page {
    margin: 5px;
  }
}
.page-body {
  display: grid;
  grid-auto-rows: min-content 1fr min-content;
  min-height: 100%;
  word-wrap: break-word;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
h1 {
  font-size: 80px;
  font-family: 'Zen Maru Gothic', "Arial", sans-serif;
  font-weight: 700;
  line-height: 90px;
  margin: 0;
  color: #FAF8F6;
}
@media (max-width: 1025px) {
  h1 {
    font-size: 70px;
  }
}
@media (max-width: 768px) {
  h1 {
    font-size: 60px;
    line-height: 1.2;
  }
}
@media (max-width: 425px) {
  h1 {
    font-size: 42px;
    line-height: 1;
  }
}
h2 {
  font-size: 62px;
  margin: 50px 0 30px;
  text-align: center;
}
@media (max-width: 1025px) {
  h2 {
    font-size: 52px;
  }
}
@media (max-width: 425px) {
  h2 {
    font-size: 32px;
    line-height: 1;
  }
}
.h2__white {
  color: #FAF8F6;
}
h3 {
  font-size: 32px;
}
@media (max-width: 1025px) {
  h3 {
    font-size: 28px;
  }
}
@media (max-width: 425px) {
  h3 {
    font-size: 22px;
    line-height: 1;
  }
}
a {
  text-decoration: none;
  font-weight: 500;
  color: #15171a;
}
a:hover {
  color: #708C69;
}
@media (max-width: 768px) {
  a {
    color: #FAF8F6;
  }
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
svg {
  stroke: none;
}
dd {
  margin: 0;
}
figure {
  margin: 0px;
}
.header {
  contain: layout;
}
.header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
/* BLACK block*/
.header__main {
  background-color: #6992B5;
  height: max-content;
  border-radius: 10px;
  padding: 150px 60px;
  margin-bottom: 10px;
}
@media (max-width: 1025px) {
  .header__main {
    padding: 60px 50px;
  }
}
@media (max-width: 768px) {
  .header__main {
    padding: 50px 30px;
    min-height: 50vh;
  }
}
@media (max-width: 425px) {
  .header__main {
    padding: 60px 30px;
    height: max-content;
  }
}
@media (max-width: 425px) {
  .header__main-card {
    padding: 90px 30px 30px;
  }
  .header__main-hobby {
    padding: 30px;
  }
}
@media (max-width: 1025px) {
  .header__privacy {
    padding: 70px 50px 110px;
  }
}
@media (max-width: 768px) {
  .header__privacy {
    padding: 80px 20px 30px;
  }
}
@media (max-width: 425px) {
  .header__privacy {
    padding: 60px 30px;
  }
}
.header__container-main {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 80px;
}
@media (max-width: 1025px) {
  .header__container-main {
    flex-wrap: wrap;
    gap: 30px;
  }
}
.header__photo {
  width: 500px;
  height: 888px;
  background-image: url('../../img/maite-900x1600.jpg');
  background-repeat: no-repeat;
  background-size: 500px 888px;
  display: block;
}
@media (max-width: 425px) {
  .header__photo {
    background-image: url('../../img/okuzminova-368x476.jpg');
    width: 193px;
    height: 238px;
    background-size: 193px 238px;
  }
}
.header__description {
  color: #FAF8F6;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 1025px) {
  .header__description {
    text-align: center;
  }
}
.header__description--position {
  text-align: center;
}
.container {
  padding-inline: calc(50% - 838px);
  margin: 0 auto;
}
.container-header {
  padding-inline: calc(60% - 838px);
  margin: 0 auto;
}
@media (max-width: 1025px) {
  .container__laptop {
    margin: 0 auto;
  }
}
.container__button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}
.link-active {
  color: #708C69;
  border: 2px solid #708C69;
}
.link-active:hover {
  background-color: #708C69;
  color: #FAF8F6;
}
.button__contact {
  margin: 0 auto;
  cursor: pointer;
}
.button {
  font-family: 'Caveat', "Arial", sans-serif;
  font-size: 24px;
  line-height: 1;
  text-decoration: none;
  display: inline-flex;
  border: none;
  cursor: pointer;
  align-items: center;
  gap: 0.75rem;
  background-color: #708C69;
  color: #fff;
  border-radius: 10rem;
  border: 2px solid #FAF8F6;
  font-weight: 600;
  padding: 0.75rem 1.5rem;
  padding-left: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: background-color 0.3s;
}
.button__header {
  font-size: 34px;
}
@media (max-width: 768px) {
  .button__header {
    font-size: 24px;
  }
}
.button__icon-wrapper {
  flex-shrink: 0;
  width: 25px;
  height: 25px;
  position: relative;
  color: var(--clr);
  background-color: #FAF8F6;
  border-radius: 50%;
  display: grid;
  place-items: center;
  overflow: hidden;
}
.button:hover {
  background-color: #444444;
}
.button:hover .button__icon-wrapper {
  color: #444444;
}
.button__icon-svg--copy {
  position: absolute;
  transform: translate(-150%, 150%);
}
.button:hover .button__icon-svg:first-child {
  transition: transform 0.3s ease-in-out;
  transform: translate(150%, -150%);
}
.button:hover .button__icon-svg--copy {
  transition: transform 0.3s ease-in-out 0.1s;
  transform: translate(0);
}
.contact {
  height: max-content;
  width: max-content;
  margin: 0 auto 100px;
}
@media (max-width: 425px) {
  .contact {
    width: 100vw;
  }
}
.contact__content {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (max-width: 1025px) {
  .contact__content {
    gap: 30px calc(2% - 10px);
  }
}
@media (max-width: 425px) {
  .contact__content {
    gap: 0px calc(2% - 10px);
  }
}
.contact__description {
  width: 500px;
  border-radius: 20px 0px 0px 20px;
  background-color: #708C69;
  padding: 50px 50px;
  color: #FAF8F6;
  text-align: center;
}
@media (max-width: 1200px) {
  .contact__description {
    text-align: center;
    padding-bottom: 20px;
  }
}
@media (max-width: 1025px) {
  .contact__description {
    text-align: center;
    font-size: 24px;
    padding-bottom: 10px;
  }
}
@media (max-width: 425px) {
  .contact__description {
    width: 330px;
    border-radius: 20px 20px 0px 0px;
    padding: 50px 10px;
    font-size: 18px;
  }
}
.contact__description h3 {
  margin: 0px 0px 20px;
  text-align: center;
}
.contact__description-block {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.contact__description-block a {
  color: #FAF8F6;
}
.contact__description-block a:hover {
  color: #135498;
}
.contact__inst::before {
  position: relative;
  content: url("../../img/icon-instagram.svg");
  left: -5px;
  top: 16px;
}
.contact__inst:hover,
.contact__wa:hover {
  background-color: #FAF8F6;
  padding: 20px;
  border-radius: 50px;
}
.contact__wa::before {
  position: relative;
  content: url("../../img/icon-whatsapp.svg");
  left: -10px;
  top: 16px;
}
.contact__content-form {
  border: 10px solid #708C69;
  border-radius: 0px 20px 20px 0px;
}
@media (max-width: 425px) {
  .contact__content-form {
    border-radius: 0px 0px 20px 20px;
  }
}
.contact__form {
  width: 500px;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
}
.contact__form label {
  margin-bottom: 40px;
}
@media (max-width: 600px) {
  .contact__form {
    width: 400px;
  }
}
@media (max-width: 425px) {
  .contact__form {
    width: 280px;
    padding: 50px 30px 50px 20px;
  }
}
.contact__label {
  width: 100%;
  padding: 20px 5px 15px;
  border: 2px solid #135498;
  border-radius: 4px;
  position: relative;
  background-color: #135498;
}
@media (max-width: 425px) {
  .contact__label {
    padding: 10px 5px 8px;
  }
}
.contact__label::after {
  position: absolute;
  content: "Name";
  font-family: 'Caveat';
  font-size: 24px;
  color: #FAF8F6;
  background-color: #135498;
  border-radius: inherit;
  top: -16px;
  left: 20px;
  width: max-content;
  margin-left: -22px;
  padding: 0px 10px;
}
@media (max-width: 425px) {
  .contact__label::after {
    top: -18px;
    font-size: 18px;
  }
}
.contact__label--en::after {
  content: "Name";
}
.contact__label:nth-child(2)::after {
  content: "Number";
}
.contact__label--en:nth-child(2)::after {
  content: "Number";
}
.contact__label:nth-child(3)::after {
  content: "Email";
}
.contact__label--en:nth-child(3)::after {
  content: "Email";
}
.contact__label:nth-child(4)::after {
  content: "Message";
}
.contact__label--en:nth-child(4)::after {
  content: "Message";
}
.contact__label:hover {
  background-color: #135498;
}
.contact__label input,
.contact__label textarea {
  width: 98%;
  font-size: 16px;
  cursor: pointer;
}
@media (max-width: 425px) {
  .contact__label input,
  .contact__label textarea {
    font-size: 14px;
  }
}
.contact__label input:hover {
  outline: none;
  border: none;
  margin-bottom: -2px;
}
.contact__label input:focus {
  outline: none;
  border: none;
  margin-bottom: -2px;
}
.contact__label textarea:hover::placeholder {
  color: #444444;
}
.contact__label input::placeholder,
.contact__label textarea::placeholder {
  color: #444444;
  font-size: 16px;
  opacity: 0.8;
}
@media (max-width: 425px) {
  .contact__label input::placeholder,
  .contact__label textarea::placeholder {
    font-size: 14px;
    color: #444444;
  }
}
.contact__label:hover > .contact__label {
  background-color: #135498;
}
.form input {
  width: calc(inherit - 10px);
  padding: 10px 0px 10px 5px;
  border: none;
  border-radius: 4px;
}
.form input:hover {
  border: 1px solid #6992B5;
  border-radius: 10px;
}
.form input:focus {
  border: 1px solid #6992B5;
  border-radius: 10px;
}
.form textarea {
  resize: none;
  width: calc(inherit - 10px);
  padding: 10px 0px 60px 5px;
  border: 1px solid transparent;
  border-radius: 4px;
}
.form textarea:hover {
  border: 1px solid #6992B5;
  border-radius: 10px;
  outline: none;
}
.form textarea:focus {
  border: 1px solid #6992B5;
  border-radius: 10px;
  outline: none;
}
.form__information {
  font-size: 14px;
  margin-bottom: 10px;
  text-align: center;
}
@media (max-width: 425px) {
  .form__information {
    margin-bottom: 8px;
  }
}
.form__information--mr {
  margin-bottom: 20px;
}
.footer {
  margin: 100px 0px 10px;
  background-color: #716D64;
  padding: 30px calc(50% - 790px);
  border-radius: 10px;
  font-size: 16px;
  color: #FAF8F6;
  text-align: center;
}
.footer a {
  color: #FAF8F6;
  text-decoration: underline;
}
.footer a:hover {
  text-decoration: none;
}
.page-header {
  display: flex;
  justify-content: center;
  background-color: #FAF8F6;
  gap: 10px;
}
@media (max-width: 768px) {
  .page-header {
    flex-direction: column;
  }
}
@media (max-width: 425px) {
  .page-header {
    gap: 0px;
  }
}
.page-header__logo {
  text-align: center;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .page-header__logo {
    flex: 0 0 100px;
  }
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 51px;
  margin-bottom: 15px;
}
@media (max-width: 768px) {
  .main-nav {
    margin-bottom: -10px;
  }
}
.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  flex: 1 0 300px;
  gap: 20px;
}
@media (min-width: 1025px) {
  .main-nav__wrapper {
    width: 65vw;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 60vw;
  }
}
@media (max-width: 768px) {
  .main-nav__wrapper {
    border-radius: 10px 10px 0px 0px;
    background-color: #15171a;
    padding: 10px 0px;
    gap: 0px;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.site-list {
  display: flex;
  gap: 20px;
}
@media (max-width: 768px) {
  .site-list {
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-grow: 1;
    gap: 10px;
  }
}
.site-list a {
  display: block;
  font-size: 22px;
}
@media (min-width: 768px) {
  .site-list a {
    padding: 15px 0px 22px;
  }
}
@media (max-width: 916px) {
  .site-list a {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  .site-list a {
    font-size: 22px;
  }
}
@media (min-width: 768px) {
  .site-list__item--active {
    position: relative;
  }
}
@media (min-width: 1200px) {
  .site-list__item--active::after {
    right: 30px;
    left: 30px;
  }
}
@media (min-width: 768px) {
  .site-list__item a[href]:active {
    color: red;
    background-color: transparent;
    opacity: 0.3;
  }
}
.site-list__item {
  width: 100%;
  display: flex;
}
@media (max-width: 768px) {
  .site-list__item:last-child {
    padding-bottom: max-content;
  }
}
.site-list__item--link {
  position: relative;
  padding: 8px 0px;
  line-height: 1.2;
}
.site-list__item--link:hover {
  border-bottom: 2px solid #708C69;
  margin-bottom: -2px;
}
.site-list__item--link:hover::after {
  border-bottom: 2px solid #708C69;
}
.user-list {
  padding-inline: 20px;
  margin-top: -5px;
  display: flex;
  gap: 20px;
}
@media (max-width: 1025px) {
  .user-list {
    padding-inline: 10px;
  }
}
@media (max-width: 768px) {
  .user-list {
    background-color: transparent;
    padding-inline: 20px;
  }
}
.user-list__item {
  padding-top: 20px;
}
.main-nav__toggle {
  position: absolute;
  top: 4px;
  right: 4px;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #15171a;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 768px) {
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 17px;
  width: 36px;
  height: 6px;
  border-radius: 10px;
  background-color: #FAF8F6;
  box-shadow: 0 12px 0 0 #FAF8F6, 0 24px 0 0 #FAF8F6;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #FAF8F6;
  box-shadow: 0 12px 0 0 #FAF8F6, 0 24px 0 0 #FAF8F6;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #FAF8F6;
  box-shadow: 0 12px 0 0 #FAF8F6, 0 24px 0 0 #FAF8F6;
}
@media (max-width: 768px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active {
    display: block;
  }
  .main-nav--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;
    padding: 10px 0px;
  }
  .main-nav--opened .main-nav__wrapper:last-child {
    border-radius: 10px;
    padding: 15px 20px 20px;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 300px;
  border-radius: 10px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 10px;
  width: 36px;
  height: 6px;
  background-color: #FAF8F6;
  border-radius: 10px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:active::before {
  background-color: #FAF8F6;
}
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #FAF8F6;
}
